		sendinfo:function(){
			
			//先获取有没有简历
			var _this=this;
			
			uni.showLoading({'title':"加载中..."});
			uni.request({
				url:_this.apiServer +"/api/v1.Resume/getResumeByUserId",
				method:'POST',
				data:{
					"id_token":uni.getStorageSync('token'),
					'accessToken': uni.getStorageSync('utoken')
					
					
				},
