					uni.hideLoading()
					 console.log(res);
					var data = res.data.data.list;
					var status = res.data.errorCode;
					
					if(status=='0'){
						
						console.log(data.id);
						//投递简历
						_this.sendfor(data.id);
						
					}else{
						
						uni.showModal({
						title: '提示',
						content: '请您先添加简历，然后在进行职位投递',
						success: function (res) {
							if (res.confirm) {
								//填写简历
								uni.navigateTo({
									url: '../resume/resume'
								});
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
						
						
					}
					
				}
			})
			
			
